/* eslint-disable @typescript-eslint/no-unused-vars */

import bannerImg from "@/shared/assets/images/homes/shop.jpg"; // Убедитесь, что путь верный
import { Button } from "@/shared/ui";
import { AiOutlineArrowRight, AiFillThunderbolt } from "react-icons/ai";

const Banner = () => (
  <section className="relative w-full h-[500px] md:h-[550px] rounded-3xl overflow-hidden shadow-2xl group isolate">

    {/* 1. ФОНОВОЕ ИЗОБРАЖЕНИЕ С ЗУМОМ */}
    <div
      className="absolute inset-0 bg-cover bg-center transition-transform duration-[2000ms] ease-out group-hover:scale-110"
      style={{ backgroundImage: `url(${bannerImg})` }}
    ></div>

    {/* 2. ЗАТЕМНЕНИЕ (Глубокий черный градиент) */}
    <div className="absolute inset-0 bg-gradient-to-r from-gray-900 via-gray-900/90 to-transparent z-10"></div>

    {/* 3. ТЕКСТУРА ШУМА (Добавляет "киношности") */}
    <div className="absolute inset-0 opacity-[0.03] z-10 pointer-events-none"
      style={{ backgroundImage: `url("https://www.transparenttextures.com/patterns/stardust.png")` }}>
    </div>

    {/* 4. БЕГУЩАЯ СТРОКА НА ЗАДНЕМ ПЛАНЕ (Декор) */}
    <div className="absolute top-10 left-0 w-full overflow-hidden opacity-10 z-10 pointer-events-none select-none">
      <div className="whitespace-nowrap animate-marquee text-[120px] font-black text-white leading-none">
        SALE SALE SALE SALE SALE SALE SALE SALE
      </div>
    </div>

    {/* 5. КОНТЕНТ */}
    <div className="relative z-20 h-full flex flex-col justify-center px-8 md:px-16 max-w-7xl mx-auto">

      <div className="flex flex-col items-start max-w-2xl">

        {/* Бейдж */}
        <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-orange-500/20 border border-orange-500/50 text-orange-400 text-xs font-bold tracking-widest uppercase mb-6 backdrop-blur-md">
          <AiFillThunderbolt />
          Ограниченное предложение
        </div>

        {/* Заголовок */}
        <h2 className="text-5xl md:text-7xl font-black text-white leading-[0.9] tracking-tighter mb-4">
          СЕЗОННАЯ <br />
          <span className="text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-red-500">
            РАСПРОДАЖА
          </span>
        </h2>

        {/* Описание скидки (Контраст тонкого и жирного) */}
        <div className="flex items-baseline gap-4 mb-8">
          <span className="text-white/60 text-xl md:text-2xl font-light">Скидки до</span>
          <span className="text-6xl md:text-8xl font-black text-white tracking-tighter">
            50%
          </span>
        </div>

        {/* Кнопка */}
        <div className="flex flex-col sm:flex-row gap-4 w-full sm:w-auto">
          <Button
            variant="primary"
            size="lg"
            className="relative overflow-hidden bg-white text-black px-8 py-4 rounded-full font-bold text-lg hover:scale-105 transition-transform flex items-center gap-2 group/btn"
            onClick={() => window.location.href = '/discount'}
          >
            <span className="relative z-10">Перейти к покупкам</span>
            <AiOutlineArrowRight className="relative z-10 transition-transform group-hover/btn:translate-x-1" />
            {/* Блик на кнопке */}
            <div className="absolute inset-0 bg-gradient-to-r from-transparent via-gray-200/50 to-transparent -translate-x-full group-hover/btn:animate-shimmer" />
          </Button>

          <p className="text-gray-500 text-xs sm:max-w-[150px] leading-tight flex items-center">
            *Предложение действует до конца месяца на выбранные товары
          </p>
        </div>

      </div>
    </div>

    {/* 6. ДЕКОРАТИВНЫЕ СВЕЧЕНИЯ (Аккуратные) */}
    <div className="absolute bottom-0 right-0 w-[500px] h-[500px] bg-orange-600/20 rounded-full blur-[120px] pointer-events-none z-10 translate-x-1/3 translate-y-1/3"></div>

  </section>
);

export default Banner;